/* Create model */

.global-model {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);

    .model-container {
        lost-center: 50%;
        background: #fff;
        margin-top: 100px;
        box-shadow: 0 0 10px #000;
        padding: 50px;
        text-align: center;
    }
}